

const DIALOGUE = "dialogue";
const ALERT = "alert";
const ERROR_MESSAGE = "errorMessage";

const UNEXPECTED = ""
const NOT_EXIST = ""
const EXIST = ""
const PERMISSION = ""

const TYPE = {

  FIREBASE_FIRESTORE: "FIREBASE_FIRESTORE",
  FIREBASE_STORAGE: "FIREBASE_STORAGE",
  FIREBASE_AUTH: "FIREBASE_AUTH",
  VALIDATIONS: "VALIDATIONS",
  VALIDATION: "VALIDATION",
  CONFIRM: "CONFIRM",
  ALERT: "ALERT",
  NETWORK: "NETWORK"

}

const APP = {
  DELETE: "app/delete",
  REQUIRE: "app/require",
  WRONG_TEXT: "app/text-violation",
  WRONG_EMAIL: "app/email-vaiolation",
  WRONG_PASSWORD: "app/password-vaiolation",
  FAIL_AGREE: "app/agreement-vaiolation",
  LENGTH_MORE: "app/form-length-more",
  LENGTH_LESS: "app/form-length-less",
  LENGTH_LESS_MB: "app/form-length-less-mb",
  LOGOUT: "app/force-logout",
  DISCONNECT: "app/internet-disconnect",
  SENDMAIL: "app/send-mail",
  NO_AUTH: "app/no-auth",
}
export { TYPE, APP };

/**
 * 
 * @param {*} error 
 */
const getMessages = (data) => {

  let messages;
  switch (data.type) {
    case TYPE.FIREBASE_FIRESTORE:
      messages = fireStoreError(data.error);
      break;
    case TYPE.FIREBASE_STORAGE:
      messages = storageError(data.error);
      break;
    case TYPE.FIREBASE_AUTH:
      messages = firebaseAuthError(data.error);
      break;
    case TYPE.VALIDATIONS:
      messages = createValidationError(data.error);
      break;
    case TYPE.NETWORK:
      messages = networkError(data.error);
      break;
    default:
      messages = [{ text: UNEXPECTED, type: ERROR_MESSAGE }];
      break;
  }

  return messages;
}

/**
 * 
 * @param {*} data 
 */
const getConfirmMessage = (data) => {

  let message = confirmMessage(data.normal, data.arg);
  return message;
}

export { getMessages, getConfirmMessage };

/**
 * firebase error
 * @param {*} error 
 */
function fireStoreError(error) {

  let message;
  switch (error) {
    case "cancelled":
    case "unknown":
    case "invalid-argument":
    case "deadline-exceeded":
    case "resource-exhausted":
    case "failed-precondition":
    case "aborted":
    case "out-of-range":
    case "internal":
    case "unavailable":
    case "data-loss":
      message = { text: UNEXPECTED, type: ERROR_MESSAGE };
      break;
    case "not-found":
      message = { text: NOT_EXIST, type: ERROR_MESSAGE };
      break;
    case "already-exists":
      message = { text: EXIST, type: ERROR_MESSAGE };
      break;
    case "permission-denied":
    case "unauthenticated":
      message = { text: PERMISSION, type: ERROR_MESSAGE };
      break;
    default:
      message = { text: UNEXPECTED, type: ERROR_MESSAGE };
      break;
  }

  return [message];
}

/**
 * storage error
 * @param {*} error 
 */
function storageError(error) {

  let message;
  switch (error) {
    case "storage/unknown":
    case "storage/object-not-found":
    case "storage/bucket-not-found":
    case "storage/project-not-found":
    case "storage/quota-exceeded":
    case "storage/invalid-checksum":
    case "storage/invalid-event-name":
    case "storage/invalid-url":
    case "storage/invalid-argument":
    case "storage/no-default-bucket":
    case "storage/cannot-slice-blob":
    case "storage/server-file-wrong-size":
      message = { text: UNEXPECTED, type: ERROR_MESSAGE };
      break;
    case "storage/canceled":
      message = { text: "", type: ERROR_MESSAGE };
      break;
    case "storage/unauthenticated":
    case "storage/unauthorized":
      message = { text: "", type: ERROR_MESSAGE };
      break;
    case "storage/retry-limit-exceeded":
      message = { text: "", type: ERROR_MESSAGE };
      break;
  }

  return [message];
}

/**
 * FirebaseAuth Error
 * @param {*} error 
 */
function firebaseAuthError(error) {

  let message;
  switch (error) {
    case "auth/email-already-in-use":
      message = { text: "", type: ERROR_MESSAGE };
      break;
    case "auth/invalid-email":
      message = { text: "", type: ERROR_MESSAGE };
      break;
    case "auth/operation-not-allowed":
      message = { text: "", type: ERROR_MESSAGE };
      break;
    case "auth/weak-password":
      message = { text: "", type: ERROR_MESSAGE };
      break;
    case "auth/user-disabled":
      message = { text: "", type: ERROR_MESSAGE };
      break;
    case "auth/user-not-found":
      message = { text: "ID", type: ERROR_MESSAGE };
      break;
    case "auth/wrong-password":
      message = { text: "", type: ERROR_MESSAGE };
      break;
    default:
      message = { text: UNEXPECTED, type: ERROR_MESSAGE };
      break;
  }

  return [message];
}

/**
 * 
 * @param {*} obj 
 */
function createValidationError(array) {

  let result = [];
  for (let i = 0; i < array.length; i++) {
    let data = array[i];
    let error = validationError(data.error, data.arg);
    result.push(error);
  }
  return result;
}


/**
 * Validation
 * @param {*} error 
 */
function validationError(error, arg) {

  let message;
  switch (error) {
    case APP.REQUIRE:
      message = { text: `${arg.name}`, type: ERROR_MESSAGE };
      break;
    case APP.WRONG_EMAIL:
      message = { text: "", type: ERROR_MESSAGE };
      break;
    case APP.WRONG_PASSWORD:
      message = { text: "", type: ERROR_MESSAGE };
      break;
    case APP.FAIL_AGREE:
      message = { text: "", type: ERROR_MESSAGE };
      break;
    case APP.LENGTH_MORE:
      message = { text: `${arg.name}${arg.length}`, type: ERROR_MESSAGE };
      break;
    case APP.LENGTH_LESS:
      message = { text: `${arg.name}${arg.length}`, type: ERROR_MESSAGE };
      break;
    case APP.LENGTH_LESS_MB:
      message = { text: `${arg.name}${arg.length}MB`, type: ERROR_MESSAGE };
      break;
    case APP.LOGOUT:
      message = { text: "", type: ERROR_MESSAGE };
      break;
    default:
      message = { text: UNEXPECTED, type: ERROR_MESSAGE };
      break;
  }

  return message;
}

/**
 * 
 * @param {*} normal 
 */
function confirmMessage(normal, arg) {

  let message;
  switch (normal) {
    case APP.DELETE:
      message = { text: `${arg.name}`, type: DIALOGUE };
      break;
    case APP.SENDMAIL:
      message = { text: "\n", type: ALERT }
      break;
    case APP.NO_AUTH:
      message = { text: "", type: ALERT }
      break;
  }

  return [message];
}


/**
 * networkError
 * @param {*} normal 
 */
function networkError(error) {

  let message;
  switch (error) {
    case APP.DISCONNECT:
      message = { text: "", type: ERROR_MESSAGE };
      break;
  }

  return [message];
}

