import { db, st } from "@/store/index"
import { TYPE, APP } from "@/containers/resorces/message"
let unit = 10000000;

const util = {

  /**==================================
   * 
   ==================================*/
  templates: {
    user: (contract, team, altId) => {

      let obj = {
        "contracts": {
        },
        "altId": `${altId}`,
        "isDeleted": false,
        "nickname": "",
        "img": false,
        "role": "manager"
      }

      obj.contracts[contract] = [team];
      return obj;
    },
    project: (date, order) => {

      return {
        "project": {
          "id": "",
          "label": "Project",
          "update_date": date,
          "order": order
        }
      }
    },
    board: (order) => {

      return {
        "board": {
          "id": "",
          "order": order,
          "label": "NewBoard",
        }
      }
    },
    initialBoards: () => {

      return [
        {
          "board": {
            "id": "",
            "label": "Backlog",
            "order": unit
          }
        },
        {
          "board": {
            "id": "",
            "label": "ToDo",
            "order": unit * 2
          }
        },
        {
          "board": {
            "id": "",
            "label": "Progress",
            "order": unit * 3
          }
        },
        {
          "board": {
            "id": "",
            "label": "Complete",
            "order": unit * 4
          }
        }
      ];
    },
    task: (uuid, date, order = null, data = null) => {

      let ordarData;
      if (order != null) {
        ordarData = order;
      }
      else {
        ordarData = unit;
      }

      let content;
      if (data != null) {

        content = data;
      } else {

        content = "";
      }

      return {
        task: {
          "id": "",
          "data": content,
          "order": ordarData,
          "labels": [],
          "members": [],
          "editing": "",
          "editing_date": null,
          "createUser": `${uuid}`,
          "create_date": date,
          "start_date": null,
          "end_date": null,
          "archive_date": null,
          "comments": []
        }
      };
    }
  },
  /**==================================
   * firebaseWrapper
   ==================================*/
  fb: {
    /**
     * CollectionAdd
     */
    add: (object) => {
      return new Promise(async (resolve, reject) => {

        if (!navigator.onLine) {
          throw { type: TYPE.NETWORK, error: APP.DISCONNECT };
        }

        let { path, content } = object;
        let collection = db.collection(path);

        await collection.add(content).then((doc) => {

          resolve(doc);

        }).catch(error => {

          throw { type: TYPE.FIREBASE_FIRESTORE, error: error.code };
        });

      });

    },
    /**
     * collection get
     */
    get: (object) => {
      return new Promise(async (resolve, reject) => {

        if (!navigator.onLine) {
          reject();
          throw { type: TYPE.NETWORK, error: APP.DISCONNECT };
        }

        let { path, key } = object;
        let collection = db.collection(path);
        await collection.get().then((querySnapshot) => {
          let array = [];
          querySnapshot.forEach((doc) => {

            let data = doc.data();
            data[key].id = doc.id;
            array.push(data);
          });
          resolve(array);

        }).catch(error => {

          throw { type: TYPE.FIREBASE_FIRESTORE, error: error.code };
        });

      });
    },
    /**
     * DocumentSet
     */
    setDoc: (object) => {
      return new Promise(async (resolve, reject) => {

        if (!navigator.onLine) {
          reject();
          throw { type: TYPE.NETWORK, error: APP.DISCONNECT };
        }

        let { path, content } = object;
        let doc = db.doc(path);

        await doc.set(content, { merge: true }).then(() => {
          resolve();

        }).catch(error => {

          throw { type: TYPE.FIREBASE_FIRESTORE, error: error.code };
        });

      });
    },
    /**
     * getDoc
     */
    getDoc: (object) => {
      return new Promise(async (resolve, reject) => {

        if (!navigator.onLine) {
          reject();
          throw { type: TYPE.NETWORK, error: APP.DISCONNECT };
        }
        let { path } = object;
        let doc = db.doc(path);
        await doc.get().then((doc) => {

          resolve(doc);

        }).catch(error => {

          throw { type: TYPE.FIREBASE_FIRESTORE, error: error.code };
        });

      });
    },
    /**
     * documentDelete
     */
    deleteDoc: (object) => {

      return new Promise(async (resolve, reject) => {

        if (!navigator.onLine) {
          reject();
          throw { type: TYPE.NETWORK, error: APP.DISCONNECT };
        }

        let { path } = object;
        let doc = db.doc(path);
        await doc.delete().then(() => {

          resolve();

        }).catch(error => {

          throw { type: TYPE.FIREBASE_FIRESTORE, error: error.code };
        });

      });
    },
    /**
     * upload
     */
    upload: (object) => {

      return new Promise(async (resolve, reject) => {

        if (!navigator.onLine) {
          reject();
          throw { type: TYPE.NETWORK, error: APP.DISCONNECT };
        }
        let { path, content } = object;

        let strorage = st.ref().child(path);
        //
        await strorage.put(content).then(() => {
          resolve();
        }).catch(error => {
          throw { type: TYPE.FIREBASE_STORAGE, error: error.code };
        });

      });
    },
    /**
     * 
     * @param {*} id 
     */
    execDownloadIcon: (id) => {

      return new Promise(async (resolve, reject) => {

        if (!navigator.onLine) {
          reject();
          throw { type: TYPE.NETWORK, error: APP.DISCONNECT };
        }

        let ref = st.ref(id + '/icon');
        let url = await ref.getDownloadURL();
        let response = await fetch(url);
        resolve(response);

      }, (error) => {
        throw { type: TYPE.FIREBASE_STORAGE, error: error.code };
      });
    }
  },
  /**==================================
   * util
   ==================================*/
  util: {

    /**
     * order
     * @param {*} id 
     * @param {*} array(projects,boards,tasks) 
     * @param {*} key:String (projects,boards,tasks) 
     */
    getOrder: (id, array, key) => {

      let prevOrder, nextOrder, myOrder = null;

      //
      //
      if (id == null && array.length == 0) {

        myOrder = unit;
      }
      //
      else if (id == null && array.length > 0) {

        prevOrder = 0;

        nextOrder = array[0][key].order;
      }
      //
      else {

        //index
        let myIndex;
        for (let i = 0; i < array.length; i++) {

          if (array[i][key].id == id) {

            myIndex = i;
          }
        }
        //index
        let prev = myIndex - 1;
        let next = myIndex + 1;


        //
        if (myIndex == 0 && array.length == 1) {

          myOrder = unit;
        }
        //
        else if (myIndex == 0 && array.length > 1) {

          prevOrder = 0;
          nextOrder = array[next][key].order;
        }
        //
        else if (myIndex == array.length - 1 && array.length > 1) {

          prevOrder = array[prev][key].order;
          myOrder = prevOrder + unit;
        }
        //
        else {
          prevOrder = array[prev][key].order;
          nextOrder = array[next][key].order;
        }

      }

      if (myOrder == null) {
        myOrder = prevOrder + (nextOrder - prevOrder) / 2;
        myOrder = prevOrder + (nextOrder - prevOrder) / 2;
      }
      return myOrder;
    }


  }
};

export default util;